





























































import { Component, Vue } from 'vue-property-decorator';

@Component
export default class Join extends Vue {
  public signin = true;
  constructor() {
    super();
  }
  public mounted() {
    this.pageCheck();
  }
  public pageCheck() {
    const page = this.$route.params.page;
    this.signin = !this.signin;
  }
}
